import React, { Component } from 'react';
import axios from 'axios';

import logo from './logo.svg';
import './App.css';
import Codemirror from 'react-codemirror';
require('codemirror/lib/codemirror.css'); //css install for code editor
require('codemirror/mode/javascript/javascript');
require('codemirror/mode/xml/xml');
require('codemirror/mode/markdown/markdown');
require('codemirror/mode/htmlmixed/htmlmixed');


var defaults = {
	markdown: '# Heading\n\nSome **bold** and _italic_ text\nBy [Jed Watson](https://github.com/JedWatson)',
	javascript: 'var component = {\n\tname: "react-codemirror",\n\tauthor: "Jed Watson",\n\trepo: "https://github.com/JedWatson/react-codemirror"\n};',
	// html:
  // `<!doctype html>
  //   <html>
  //     <head>
  //       <meta charset=utf-8>
  //       <title>HTML5 canvas demo</title>
  //       <style>p {font-family: monospace;}</style>
  //     </head>
  //     <body>
  //       <p>Canvas pane goes here:</p>
  //       <canvas id=pane width=300 height=200></canvas>
  //       <script>
  //         var canvas = document.getElementById('pane');
  //         var context = canvas.getContext('2d');
  //         context.fillStyle = 'rgb(250,0,0)';
  //         context.fillRect(10, 10, 55, 50);
  //         context.fillStyle = 'rgba(0, 0, 250, 0.5)';
  //         context.fillRect(30, 30, 55, 50);
  //       </script>
  //     </body>
  //   </html>`
  html:
  `
	<!DOCTYPE html>
	<html lang="en">
		<head>
			<title>three.js - ASCII Effect</title>
			<meta charset="utf-8">
			<meta name="viewport" content="width=device-width, user-scalable=no, minimum-scale=1.0, maximum-scale=1.0">
			<style>
				body {
					font-family: Monospace;
					background-color: #f0f0f0;
					margin: 0px;
					overflow: hidden;
				}
			</style>
		</head>
		<body>

			<script src="three.js/build/three.js"></script>

			<script src="three.js/examples/js/controls/TrackballControls.js"></script>
			<script src="three.js/examples/js/effects/AsciiEffect.js"></script>

			<script src="three.js/examples/js/renderers/Projector.js"></script>
			<script src="three.js/examples/js/renderers/CanvasRenderer.js"></script>

			<script src="three.js/examples/js/libs/stats.min.js"></script>

			<script>

				var container, stats;

				var camera, controls, scene, renderer;

				var sphere, plane;

				var start = Date.now();

				init();
				animate();

				function init() {

					var width = window.innerWidth || 2;
					var height = window.innerHeight || 2;

					container = document.createElement( 'div' );
					document.body.appendChild( container );

					var info = document.createElement( 'div' );
					info.style.position = 'absolute';
					info.style.top = '10px';
					info.style.width = '100%';
					info.style.textAlign = 'center';
					info.innerHTML = 'Drag to change the view';
					container.appendChild( info );

					camera = new THREE.PerspectiveCamera( 70, width / height, 1, 1000 );
					camera.position.y = 150;
					camera.position.z = 500;

					controls = new THREE.TrackballControls( camera );

					scene = new THREE.Scene();

					var light = new THREE.PointLight( 0xffffff );
					light.position.set( 500, 500, 500 );
					scene.add( light );

					var light = new THREE.PointLight( 0xffffff, 0.25 );
					light.position.set( - 500, - 500, - 500 );
					scene.add( light );

					sphere = new THREE.Mesh( new THREE.SphereGeometry( 200, 20, 10 ), new THREE.MeshLambertMaterial() );
					scene.add( sphere );

					// Plane

					plane = new THREE.Mesh( new THREE.PlaneBufferGeometry( 400, 400 ), new THREE.MeshBasicMaterial( { color: 0xe0e0e0 } ) );
					plane.position.y = - 200;
					plane.rotation.x = - Math.PI / 2;
					scene.add( plane );

					renderer = new THREE.CanvasRenderer();
					renderer.setClearColor( 0xf0f0f0 );
					renderer.setSize( width, height );
					// container.appendChild( renderer.domElement );

					effect = new THREE.AsciiEffect( renderer );
					effect.setSize( width, height );
					container.appendChild( effect.domElement );

					stats = new Stats();
					container.appendChild( stats.dom );

					//

					window.addEventListener( 'resize', onWindowResize, false );

				}

				function onWindowResize() {

					camera.aspect = window.innerWidth / window.innerHeight;
					camera.updateProjectionMatrix();

					renderer.setSize( window.innerWidth, window.innerHeight );
					effect.setSize( window.innerWidth, window.innerHeight );

				}

				//

				function animate() {

					requestAnimationFrame( animate );

					stats.begin();
					render();
					stats.end();

				}

				function render() {

					var timer = Date.now() - start;

					sphere.position.y = Math.abs( Math.sin( timer * 0.002 ) ) * 150;
					sphere.rotation.x = timer * 0.0003;
					sphere.rotation.z = timer * 0.0002;

					controls.update();

					effect.render( scene, camera );

				}

			</script>
		</body>
	</html>

  `
};

class App extends Component {
  constructor(){
    super();
    this.state={
      code: defaults.html,
			readOnly: false,
			mode: 'htmlmixed'
    }
    this._updateCode=this._updateCode.bind(this);
  }
  _updateCode (newCode) {
		this.setState({
			code: newCode
		});
	}
  interact (cm) {
		// console.log(cm.getValue());
	}
	_updateCanvas(){
		var previewFrame = document.getElementById('preview');
		var preview =  previewFrame.contentDocument ||  previewFrame.contentWindow.document;
		preview.open();
		preview.write(this.state.code);
		preview.close();
	}
  componentDidUpdate(){
        this._updateCanvas();
	}

  componentDidMount(){
    this._updateCanvas();
		axios.get('three.js/examples/canvas_ascii_effect.html')
		.then(function (response) {
    // console.log(response.data);
		defaults.html = response.data;
		// console.log(defaults)
  })
  .catch(function (error) {
    console.log(error);
  });
  }

  render() {
    var options = {
			lineNumbers: true,
			readOnly: this.state.readOnly,
			mode: this.state.mode
		};
    return (
      <div className="App">
        <Codemirror ref="editor" value={this.state.code} onChange={this._updateCode} options={options} interact={this.interact} height="10px"/>
        <iframe id="preview" width="100%" height="600px"></iframe>
      </div>
    );
  }
}

export default App;



